"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Card } from "@/components/ui/card";

export default function Home() {
  const [inputText, setInputText] = useState("");
  const [result, setResult] = useState("");
  const [isResultView, setIsResultView] = useState(false);
  const [error, setError] = useState("");

  const handleClear = () => {
    setInputText("");
    setError("");
  };

  const handleSort = () => {
    setError("");
    const lines = inputText.split("\n").filter((line) => line.trim() !== "");
    
    if (lines.length === 0) {
      setError("Por favor, insira pelo menos uma linha válida!");
      return;
    }
    
    const randomIndex = Math.floor(Math.random() * lines.length);
    setResult(lines[randomIndex]);
    setIsResultView(true);
  };

  const handleReSort = () => {
    setError("");
    const lines = inputText.split("\n").filter((line) => line.trim() !== "");
    
    if (lines.length === 0) {
      setError("Por favor, insira pelo menos uma linha válida!");
      return;
    }
    
    const randomIndex = Math.floor(Math.random() * lines.length);
    setResult(lines[randomIndex]);
  };

  const handleBack = () => {
    setIsResultView(false);
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center bg-[#fafafa] p-4">
      {!isResultView ? (
        <div className="w-full max-w-md space-y-6">
          <div className="space-y-2 text-center">
            <h1 className="text-3xl font-bold tracking-tighter">
              Sorteador de Palavras
            </h1>
            <p className="text-gray-500 dark:text-gray-400">
              Digite uma ou mais linhas e sorteie uma delas aleatoriamente.
            </p>
          </div>
          <Card className="p-6">
            <Textarea
              placeholder="Digite uma ou mais linhas..."
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              className="min-h-[200px] mb-4"
            />
            {error && (
              <div className="text-red-500 text-sm mb-4">
                {error}
              </div>
            )}
            <div className="flex gap-4">
              <Button 
                onClick={handleSort}
                className="flex-1"
              >
                Sortear
              </Button>
              <Button 
                onClick={handleClear}
                variant="outline"
                className="flex-1"
              >
                Limpar
              </Button>
            </div>
          </Card>
        </div>
      ) : (
        <div className="w-full max-w-md">
          <Card className="p-6">
            <div className="text-center space-y-6">
              <div className="space-y-2">
                <h2 className="text-xl font-semibold">Resultado do Sorteio</h2>
                <div className="p-4 bg-gray-50 rounded-lg border">
                  <p className="text-2xl font-bold break-words">{result}</p>
                </div>
              </div>
              <div className="flex gap-4">
                <Button 
                  onClick={handleReSort}
                  className="flex-1"
                >
                  Sortear Novamente
                </Button>
                <Button 
                  onClick={handleBack}
                  variant="outline"
                  className="flex-1"
                >
                  Voltar
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}
    </main>
  );
}
